html {
    font-size: $font_size;
}

body {
    background-color: $background_color;
    color: $text_color;
    font-family: $base_fonts, $emoji_fonts;
    line-height: 1.5;
    padding: 0 1rem;
}

a {
    color: $link;

    &:visited {
        color: $visited_link;
    }

    &:hover {
        color: $hover_link;
    }
}

h1, h2 {
    font-size: 1rem;
    font-weight: bold;
    margin-top: 2rem;
}

sup {
    line-height: 0;
    position: relative;
}

#chi-sono {
    margin: 3rem 0 3rem 3rem;
    font-size: 1.2rem;
    max-width: 34rem;

    em {
        text-decoration: underline;
    }

    .pulsante {
        display: block;
        margin: 2rem auto;
        width: 10rem;

        svg #background-splash {
            fill: #ffd500;
            opacity: 0;
            transition: .2s;
        }

        &:hover svg #background-splash {
            opacity: 1;
        }
    }
}