#footer {
    color: $site_footer;
    font-size: .7rem;
    width: 100%;
    color: $dimmed_text;
    clear: both;

    #altre-pagine {
        a {
            margin-right: .2rem;
        }
    }
    
    #impressum {
        margin-top: 1rem;
    }

    a {
        color: $site_footer;

        &:hover {
            color: $site_footer_hover_link;
        }
    }

    p {
        margin: 0;
    }
}