#header {
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    #logo {
        height: 2rem;

        svg {
            height: 100%;
            #nome {
               fill: #000000;
            }

            #background-splash {
                fill: #ffd500;
                opacity: 0;
                transition: .2s;
            }

            #riga {
                fill: #af0303;
                opacity: 0;
            }
        }

        &:hover svg #background-splash {
            opacity: 1;
        }

        &.home {
            svg #riga {
                opacity: 1;
            }
            
            &:hover svg #background-splash {
                    opacity: 0;
            }
        }
    }

    #pagine {
        font-weight: 300;
        color: $dimmed_text;

        a {
            margin-left: 1rem;
        }
    }
}