$base_fonts: "Karla", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
$emoji_fonts: "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
$mono_fonts: "Courier Prime Sans", monospace;
$font_size: 18px;

$max_width: 36rem;

// Light theme colors
$background_color: #fff;
$footer_background_color: #e7e3cd;
$text_color: #000;
$link: #000;
$visited_link: #003bdf;
$hover_link: #f405a1;
$dimmed_text: #8e8e8e;
$slightly_dimmed_text: #8e8e8e;
$site_footer: $text_color;
$site_footer_hover_link: #c85443;
$button: #f7a000;

// Dark theme colors
$dark_background_color: #000000;
$dark_footer_background_color: #c36d00;
$dark_text_color: #ffffff;
$dark_link: #ffffff;
$dark_visited_link: #01b6a7;
$dark_hover_link: #02d67e;
$dark_dimmed_text: #cecece;
$dark_slightly_dimmed_text: #8e8e8e;
$dark_site_footer_text: #ffffff;
$dark_site_footer_link: #e208be;
$dark_site_footer_hover_link: #5f08ff;

@import "fonts";

@import "base";
@import "base_dark";
@import "header";
@import "header_dark";
@import "footer";
@import "footer_dark";

@import "single";
@import "single_dark";
@import "micro";

@import "bio";
@import "contacts";
@import "contacts_dark";